.new-confirm {
	padding: 20px 0;
	min-height: 380px;
	@include r2 {
		padding: 16px 0 24px;
		min-height: 1px; }
	&__box {
		@include clr;
		@include r2 {
			margin: 0 auto;
			max-width: 250px; } }
	&__label {
		width: 40%;
		float: left;
		padding: 6px 30px 4px;
		@include r1 {
			padding: 1px 30px; }
		@include r2 {
			width: 100%;
			float: none;
			padding: 0 0 8px; }
		.label {
			max-width: 175px;
			@include r2 {
				max-width: 100%; } } }
	&__value {
		position: relative;
		max-width: 250px;
		padding: 6px 50px 20px 0;
		width: 60%;
		float: left;
		&_pull-right {
			margin-left: 40%;
			@include r2 {
				margin-left: 0; } }
		@include r1 {
			padding: 1px 50px 20px 0; }
		@include r2 {
			width: 100%;
			float: none;
			padding: 0 50px 16px 0; } }
	&__list {
		@include hr;
		font-size: 13px;
		line-height: 20px;
		span {
			color: $gray; }
		em {
			color: $dark;
			font-style: normal;
			font-weight: normal; } }
	&__edit {
		position: absolute;
		top: -5px;
		right: 0; }
	&__total {
		color: $blue;
		@include as;
		font-size: 18px;
		line-height: 20px; }
	&__footer {
		padding-top: 20px;
		font-size: 0;
		text-align: center; } }

