.courier {
	&__body {
		min-height: 380px;
		border-bottom: 1px solid #d9dbe1;
		padding: 20px 0;
		@include r2 {
			padding: 16px 0 24px;
			min-height: 1px; } }
	&__box {
		@include clr;
		padding: 0 20px;
		@include r2 {
			padding: 0; } }
	&__label {
		width: 40%;
		float: left;
		padding: 6px 30px 4px;
		@include r2 {
			width: 100%;
			float: none;
			padding: 0 0 8px; } }
	&__field {
		width: 60%;
		float: left;
		position: relative;
		@include r2 {
			width: 100%;
			float: none; }
		p {
			padding-top: 16px; }
		.radio, .checkbox {
			span {
				padding-left: 10px;
				@include r2 {
					padding-left: 0; } }
			strong {
				color: $gray; } }
		.icon-check {
			position: absolute;
			top: 16px;
			right: 20px;
			font-size: 10px;
			fill: $blue;
			@include r1 {
				top: 11px;
				right: 16px; } }
		.btn {
			position: absolute;
			top: 0;
			right: 0;
			min-width: 110px;
			@include r2 {
				position: relative;
				margin-bottom: 16px; } } }
	&__select {
		position: relative;
		padding: 0 130px 20px 0;
		@include r1 {
			padding: 4px 130px 16px 0; }
		@include r2 {
			padding: 0 0 16px; } }
	&__radio {
		padding-bottom: 10px;
		@include r2 {
			padding-bottom: 8px; }
		li {
			@include clr;
			border-top: 1px solid #d9dbe1;
			@include r3 {
				padding-top: 8px; }
			&:first-child {
				border-top: none; } }
		.radio {
			float: left;
			@include r3 {
				float: none; } } }
	&__price {
		float: right;
		font-size: 13px;
		line-height: 30px;
		letter-spacing: 0.19px;
		color: $dark;
		@include r3 {
			float: none; }
		a {
			color: $blue;
			@include hover {
				text-decoration: underline; } } }
	&__checkbox {
		padding-bottom: 20px;
		@include r2 {
			padding-bottom: 16px; }
		.checkbox {
			span {
				color: $dark; } } }
	&__textarea {
		.textarea {
			height: 90px; } }
	&__address {
		padding: 0 130px 20px 0;
		@include r1 {
			padding: 0 130px 12px 0; }
		@include r2 {
			padding: 0 0 20px; } } }
