.data {
	&__body {
		padding: 20px 0;
		border-bottom: 1px solid #d9dbe1;
		min-height: 380px;
		@include r2 {
			padding: 16px 0 24px;
			min-height: 1px; } }
	&__label {
		width: 40%;
		float: left;
		padding: 6px 30px 4px;
		@include r1 {
			padding: 1px 30px; }
		@include r2 {
			width: 100%;
			float: none;
			padding: 0 0 8px; }
		.label {
			max-width: 175px;
			@include r2 {
				max-width: 100%; } } }
	&__box {
		@include clr;
		padding: 0 20px;
		@include r2 {
			padding: 0; }
		&:last-child {
			.form-person__field {
				padding-bottom: 0; } } }
	&__field {
		max-width: 310px;
		padding-bottom: 20px;
		width: 60%;
		float: left;
		&_pull-right {
			margin-left: 40%;
			@include r2 {
				margin-left: 0; } }
		@include r2 {
			width: 100%;
			float: none;
			padding-bottom: 16px; } }
	&__container {
		padding: 20px 0 0; } }
