.tooltipster-box {
	width: 160px; }
.tooltipster-base {
	display: block; }
.tooltipster-sidetip {
	.tooltipster-box {
		border: none;
		border-radius: 2px;
		background-color: #f5f5f5;
		box-shadow: 0 6px 12px 0 rgba(62, 76, 102, 0.2); }
	.tooltipster-content {
		@include hr;
		font-size: 13px;
		line-height: 1.54;
		color: #727b8a;
		padding: 10px 10px 10px 20px;
		position: relative;
		&:before {
			content: "";
			position: absolute;
			top: 10px;
			left: 0;
			height: 20px;
			width: 2px;
			border-radius: 1px;
			background: $blue; } } }
