.header {
	background: url(../img/header.jpg) no-repeat center center;
	background-size: cover;
	position: relative;
	@include clr;
	&_big {
		height: 500px;
		@include r1 {
			height: 315px; }
		@include r2 {
			height: 240px; } }
	&_small {
		.nav {
			margin-bottom: -30px; } }
	&_card {
		background-image: url(../img/header-card.jpg);
		height: 700px;
		margin-bottom: -470px;
		@include r1 {
			margin-bottom: 0;
			height: auto; } }
	&_landing {
		.header__text {
			@include r1 {
				font-size: 10px;
				letter-spacing: 0.2px;
				line-height: 11.25px; } } }
	&__inner, &__main {
		@extend %inner;
		padding: 40px 20px 30px;
		z-index: 1015; }
	&__inner {
		@include r1 {
			display: none; } }
	&__main {
		@include r1 {
			// padding: 24px 20px 30px
			padding: 106px 20px 30px; }
		@include r2 {
 } }			// padding: 32px 20px 30px
	&__schedule {
		float: left;
		margin-right: 28px; }
	&__phone {
		@include as;
		color: white;
		font-size: 22px;
		letter-spacing: -0.53px;
		line-height: 1.3; }
	&__text, &__callback-btn span {
		color: #d9dbe1;
		font-size: 13px;
		line-height: 1.15;
		letter-spacing: 0.26px; }
	&__callback {
		float: left;
		font-size: 0;
		position: relative;
		&-btn {
			padding: 18px 0 17px 60px;
			position: relative;
			display: block;
			.no-touch & {
				&:hover {
					.header__icon {
						background: whitesmoke;
						svg {
							fill: $blue; } }
					span {
						border-color: transparent;
						color: #fff;
						&:before {
							opacity: 1; } } } }
			&.is-active {
				span {
					border-color: transparent;
					color: #fff;
					&:before {
						opacity: 1; } } }
			.header__icon {
				position: absolute;
				top: 0;
				left: 0; }
			span {
				display: inline-block;
				vertical-align: top;
				border-bottom: 1px dashed #d9dbe1;
				position: relative;
				transition: color 0.25s, border-color 0.15s;
				&:before {
					content: "";
					position: absolute;
					bottom: -8px;
					left: 50%;
					margin-left: -15px;
					width: 30px;
					height: 2px;
					background: $blue;
					box-shadow: 0 2px 4px rgba(65, 123, 231, 0.3);
					opacity: 0;
					transition: opacity 0.25s; } } } }
	&__actions {
		float: right;
		font-size: 0;
		// li
		// 	padding: 0 10px
		// 	display: inline-block
		// 	vertical-align: top
		// 	position: relative
		// 	z-index: 1
		// span
		// 	position: absolute
		// 	top: 15px
		// 	right: -12px
		// 	width: 20px
		// 	height: 20px
		// 	background-color: whitesmoke
		// 	box-shadow: 0 2px 4px rgba(62, 76, 102, 0.2)
		// 	+as
		// 	color: $blue
		// 	font-size: 16px
		// 	letter-spacing: -0.45px
		// 	line-height: 1.25
		// 	text-align: center
 }		// 	border-radius: 50%
	&__heart, &__search, &__basket {
		padding: 0 10px;
		display: inline-block;
		vertical-align: top;
		position: relative;
		z-index: 1; }
	&__icon {
		display: block;
		width: 50px;
		height: 50px;
		border: 2px solid rgba(#d9dbe1, 0.6);
		position: relative;
		border-radius: 50%;
		text-align: center;
		@include vertical;
		transition: background 0.25s ease-out;
		z-index: 2;
		background: none;
		.no-touch & {
			&:hover {
				background-color: whitesmoke;
				svg {
					fill: $blue; } } }
		svg {
			display: inline-block;
			vertical-align: middle;
			fill: #fff;
			transition: fill 0.25s ease-out; }
		.icon-phone {
			font-size: 21px; }
		.icon-heart {
			font-size: 18px;
			margin-top: 2px; }
		.icon-search {
			font-size: 18px;
			margin-top: 2px; }
		.icon-basket {
			font-size: 20px; }
		span {
			position: absolute;
			top: 50%;
			left: 100%;

			overflow: hidden;
			padding: 0 4px;
			margin: -10px 0 0 -10px;
			border-radius: 10px;
			min-width: 20px;
			max-width: 40px;
			height: 20px;

			color: $blue;
			background: whitesmoke;
			box-shadow: 0 2px 4px rgba(62, 76, 102, 0.2);

			@include as;
			font-size: 16px;
			line-height: 20px;
			letter-spacing: 0.45px;

			white-space: nowrap;
			text-overflow: ellipsis; } }
	&__logo {
		padding: 10px 390px 0;
		text-align: center;
		margin-top: -7px;
		@include r1 {
			margin: 0;
			padding: 0; } }
	&__search {
		width: 70px;
		height: 50px; }
	&-search {
		position: absolute;
		top: 0;
		right: 10px;
		width: 50px;
		height: 50px;
		transition: width 0.3s ease-out, background 0.3s ease-out, border-color 0.25s ease-out;
		border-radius: 30px;
		border: 2px solid rgba(#d9dbe1, 0.6);
		z-index: 110;
		&.is-active {
			background: whitesmoke; }
		.no-touch & {
			&:hover {
				background: whitesmoke; } }
		&.is-active {
			width: 300px;
			.header {
				&__icon_search {
					svg {
						display: none; }
					i {
						display: block; } } }
			.sm-search {
				overflow: visible;
				.sm-search {
					&__input {
						opacity: 1; } } } }
		&.is-focus {
			border-color: $blue; } }
	&__icon_search {
		position: absolute;
		top: -2px;
		right: -2px;
		width: 50px;
		height: 50px;
		border: none;
		.no-touch & {
			&:hover {
				background: none; } } }
	&__close {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate3d(-50%, -50%, 0) rotate(45deg);
		width: 10px;
		height: 10px;
		display: none;
		&:before, &:after {
			content: "";
			position: absolute; }
		&:before {
			border-left: 2px solid $blue;
			top: 0;
			bottom: 0;
			left: 50%;
			margin-left: -1px; }
		&:after {
			border-bottom: 2px solid $blue;
			top: 50%;
			left: 0;
			right: 0;
			margin-top: -1px; } } }


.sm-search {
	border-radius: 30px;
	overflow: hidden;
	font-size: 0;
	background: none;
	width: 100%;
	&__input {
		opacity: 0;
		transition: opacity 0.2s ease-in;
		// width: 100%
		width: 296px;
		padding: 16px 46px 15px 18px;
		background: none;
		font-size: 13px;
		@include hr;
		letter-spacing: 0.2px;
		color: $dark;
		&::placeholder {
