//menu
.menu {
	position: absolute;
	top: 100%;
	left: 0;
	right: 0;
	padding: 10px 0 0;
	visibility: hidden;
	opacity: 0;
	will-change: opacity;
	&__inner {
		padding: 20px 20px 7px;
		background: whitesmoke;
		border-radius: 2px;
		box-shadow: 0 6px 12px 0 rgba(62, 76, 102, 0.2);
		font-size: 0;
		text-align: left; }
	&__box {
		width: 25%;
		display: inline-block;
		vertical-align: top; }
	&__title {
		font-size: 11px;
		text-transform: uppercase;
		line-height: 2.73;
		@include hm;
		color: $dark;
		padding-bottom: 2px;
		transition: color 0.25s ease-out;
		@include hover {
			color: $blue; } }
	&__list {
		padding-bottom: 13px;
		ul {
			a {
				font-size: 13px;
				color: $gray;
				transition: color 0.25s ease-out;
				@include hr;
				line-height: 1.92;
				display: block;
				.no-touch & {
					&:hover {
						color: $blue; } } } } } }

