		margin-top: -4px;
		&:before, &:after {
			content: "";
			position: absolute; }
		&:before {
			border-left: 2px solid $blue;
			top: 0;
			bottom: 0;
			left: 50%;
			margin-left: -1px; }
		&:after {
			border-bottom: 2px solid $blue;
			top: 50%;
			left: 0;
			right: 0;
			margin-top: -1px; } }
	&__title, &__list small {
		letter-spacing: 0.2px; }
	&__list {
		padding: 7px 0 3px;
		border-top: 1px solid #d9dbe1;
		display: none;
		a {
			@include clr;
			padding-left: 8px; }
		span {
			display: block;
			overflow: hidden; }
		small {
			float: right;
			min-width: 20px;
			text-align: right;
			font-size: 11px;
			color: #969da9;
			padding: 8px 0 0; } }
	&__head, &__title {
		&.is-active {
			.mob-item__plus {
				transform: rotate(45deg) scale(1.2); } } } }

//callback
.callback {
	left: 60px;
	width: 240px;
	padding: 15px 20px;
	&.is-active {
		opacity: 1;
		visibility: visible; }
	&__field {
		padding: 5px 0; }
	.btn {
		width: 100%; } }

.sm-popup-open {
	position: relative;
	.no-touch & {
		&:hover {
			.sm-popup {
				opacity: 1;
				visibility: visible; } } } }

.sm-popup {
	position: absolute;
	top: 100%;
	background-color: whitesmoke;
	border-radius: 2px;
	box-shadow: 0 6px 12px rgba(62, 76, 102, 0.2);
	visibility: hidden;
	opacity: 0;
	transition: opacity 0.25s ease-out, visibility 0.25s ease-out;
	&_top {
		margin-top: 10px;
		&:before {
			content: "";
			height: 10px;
			top: -10px;
			left: 0;
			right: 0;
			position: absolute;
			opacity: 0; } }
	&__inner {
		padding: 20px; }
	&__title {
		font-size: 18px;
		line-height: 20px;
		letter-spacing: 0.24px;
		@include as;
		color: $dark;
		margin: 0 -20px 20px;
		padding: 0 20px;
		position: relative;
		&:before {
			content: "";
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			width: 2px;
			border-radius: 1px;
			background: $blue; } } }

.sm-city {
	left: 0;
	text-align: center;
	width: 300px;
	&__text {
		font-size: 13px;
		line-height: 25px;
		color: $gray;
		@include hr;
		margin-bottom: 15px; }
	&__btns {
		margin: 0 -10px;
		font-size: 0;
		.btn {
			margin: 0 10px; } }
	&__submit {
		min-width: 80px; }
	&__cancel {
		width: 160px; } }

.sm-basket {
	width: 240px;
	right: 10px;
	&__list {
		margin-bottom: 20px;
		font-size: 13px;
		line-height: 20px;
		@include hr;
		li {
			@include clr; } }
	&__label, &__text {
		float: left; }
	&__label {
		color: $gray;
		width: 80px; }
	&__text {
		color: $blue; }
	.btn {
		width: 100%; } }

.sm-profile {
	width: 150px;
	right: 0;
	&.is-active {
		opacity: 1;
		visibility: visible; }
	&__link {
		display: block;
		color: $gray;
		font-size: 13px;
		line-height: 25px;
		@include hr;
		transition: color 0.25s ease-out;
		.no-touch & {
			&:hover {
				color: $blue; } } }
	&__nav {
		padding-bottom: 10px;
		border-bottom: 1px solid #d9dbe1;
		margin-bottom: 9px; } }
