		@include r1 {
			width: 480px; }
		@include r2 {
			right: -35px;
			width: 330px; }
		img {
			width: 100%; } }
	&__left {
		float: left;
		padding: 0 0 0 50px;
		margin-top: -80px;
		width: 48%;
		@include r1 {
			padding: 80px 0;
			margin: 0;
			width: 100%; }
		@include r2 {
			padding: 32px 0; } }
	&__info {
		position: relative;
		z-index: 15;
		padding: 85px 0 130px 100px;
		max-width: 340px;
		@include r1 {
			float: right;
			padding: 48px 55px 85px  0;
			max-width: 280px; }
		@include r2 {
			float: none;
			padding: 48px 16px 85px;
			max-width: 255px; } }
	&__title {
		margin: 0 0 29px;

		color: $blue;

		@include as;
		font-size: 28px;
		line-height: (30/28);
		@include r1 {
			margin: 0 0 16px;
			font-size: 24px;
			line-height: 1; }
		&:after {
			content: "";

			display: block;
			margin: 14px 0 0;
			border-radius: 1px;
			width: 20px;
			height: 2px;
			background: $blue;
			@include r1 {
				margin: 8px 0 0;
				width: 16px; } } } }

.landing-subs {
	padding: 0 20px;
	@include r1 {
		padding: 0 24px; }
	@include r2 {
		padding: 0 16px; } }
